<script lang="ts">
	import { onMount } from 'svelte';
	import { setContext} from "svelte";
	import {writable} from 'svelte/store';
	import Word from "./Word.svelte";
	import { WORDS } from "./words.ts";
	import random from 'random';
	//const seedrandom = require('seedrandom');
	import seedrandom from "seedrandom";
	import Keyboard from "./Keyboard.svelte";

	const letters = "abcdefghijklmnopqrstuvwxyz".split("");


	//export let name: string;
	export let guess = '';
	export let answer = 'hunch';
	let guesses = [
		{id: 0, guess: ''},
		{id: 1, guess: ''},
		{id: 2, guess: ''},
		{id: 3, guess: ''},
		{id: 4, guess: ''},
		{id: 5, guess: ''}
	];
	let guessNum = 0;
	let correct = false;
	let gameOver = false;

	let _letterStates = {};
	for (let letter of letters) {
		_letterStates[letter] = 'correct';
	}
	let letterStates = writable(_letterStates)
	setContext('letterStates', letterStates);

	/*$: if (guess.length > answer.length) {
		//console.log("answer is too long!");
		guess = guess.slice(0, answer.length);
	}*/
	$: if (guess) {
		//console.log("guess before:", guess);
		guess = guess.replace(/[^A-Za-z]/gi, '');
		//console.log("guess after:", guess);
	}

	onMount( () => {
		let today = new Date().toISOString().slice(0, 10);
		//console.log("random seed", today);
		//random.use(seedrandom(today));
		let index = random.int(0, WORDS.length-1);
		answer = WORDS[index];
		console.log("answer is:", answer);
	});

	/*function handleEnter() {
		submitGuess();
	}*/

	function handleNewLetter(event) {
		if (gameOver) {
			return;
		}
		const letter = event.detail.letter;
		if (guess.length < answer.length && letter.length === 1) {
			guess = guess + event.detail.letter;
			guesses[guessNum]['guess'] = guess;
		} else if (letter.toLowerCase() === 'del') {
			if (guess.length > 0) {
				guess = guess.slice(0, guess.length-1);
			}
			guesses[guessNum]['guess'] = guess;
		} else if (letter.toLowerCase() === 'enter') {
			submitGuess();
		}
	}

	function submitGuess() {
		const g = guess.toLowerCase();

		if (g.length !== answer.length) {
			return;
		}
		if (!WORDS.includes(g)) {
			return;
		}
		if (g === answer.toLowerCase()) {
			//console.log("got the right answer!");
			correct = true;
			gameOver = true;
			console.log("game over... you win!")
		}

		letterStates.update(o => {
			for (let letter of g) {
				if (!answer.includes(letter)) {
					o[letter] = 'incorrect';
				}
			}
			return o;
		});
		//letterStates = letterStates;
		//console.log(letterStates)

		//guesses = [...guesses, {id: guesses.length + 1, guess: guess}];
		guesses[guessNum]['guess'] = guess;
		//guesses = guesses;
		guessNum++;
		guess = '';
		if (guessNum >= guesses.length) {
			gameOver = true;
			console.log("game over!")
		}
		//console.log(guesses);

		if (gameOver) {
			if (correct) {
				setTimeout(() => alert("You got the word: " + answer), 250);
			} else {
				setTimeout(() => alert("You lost! Answer: " + answer), 250);
			}
		}
	}

	function onKeyPress(e) {
		if (e.charCode === 13) {
			submitGuess();
		}
	    //guess = guess.replace(/[^A-Za-z]/gi, '');
		//console.log("guess is", guess)
	}

</script>

<main>
	<!--<p>Debug: the answer is... "{answer}"</p> -->

	<h1>SOLUDLE</h1>
	<ul class="guess-list">
		{#each guesses as g (g.id)}
			<li>
				<Word guess="{g.guess}" answer="{answer}" reveal="{g.id < guessNum}"/>
			</li>
		{/each}
	</ul>

	<!--<input maxlength={answer.length}
		   on:keypress={onKeyPress}
		   bind:value={guess} placeholder="enter your guess"
		   style="text-transform:uppercase">
	-->

	<Keyboard on:newLetter={handleNewLetter} />

	<!--
	<button on:click={handleEnter} disabled={guess.length !== answer.length}>Enter</button>
	-->
</main>

