	main {
		text-align: center;
		padding: 0.2em;
		margin: 0 auto;
	}

	:global(body) {
		background-color: #2a2a2a;
	}

	h1 {
		color: #ffffff;
		text-transform: uppercase;
		font-size: 2em;
		font-weight: 100;
	}

	@media (min-width: 640px) {
		main {
			max-width: none;
		}
	}

	.guess-list {
		list-style-type: none;
		padding: 0;
		margin: 0;
		margin-top: 1em;
		margin-bottom: 1em;
		white-space: nowrap;
	}
	.guess-list > li {
		margin-bottom: 0.5em;
	}
</style>