<script lang="ts">
    import Letterbox from "./Letterbox.svelte";
    import {createEventDispatcher, getContext} from "svelte";

    /*
    $: outerWidth = 0
    $: innerWidth = 0
    $: outerHeight = 0
    $: innerHeight = 0
     */

    //let key;
    //let keyCode;

    const letterStates = getContext('letterStates');
    const rows = [
        ['q','w','e','r','t','y','u','i','o','p'],
        ['a','s','d','f','g','h','j','k','l'],
        ['Del', 'z','x','c','v','b','n','m', 'Enter']
    ];

    const dispatch = createEventDispatcher();

    function clickLetter(letter) {
        //console.log("Pressed letter!", letter)
        dispatch('newLetter', {
            letter: letter
        });
    }

    function handleKeydown(event) {
        let key = event.key;
        let keyCode = event.keyCode;
        if (keyCode >= 65 && keyCode <= 90) {
            clickLetter(key);
        } else if (keyCode === 13) {
            clickLetter('Enter');
        } else if (keyCode === 8) {
            clickLetter('Del');
        }
    }

</script>

<!--
<svelte:window bind:innerWidth bind:outerWidth bind:innerHeight bind:outerHeight />
-->
<svelte:window on:keydown={handleKeydown}/>

<div class="keyboard">
    {#each rows as row (row[0])}
    <ul class="letter-list">
        {#each row as letter (letter)}
        <li class="word-letters" on:click={() => clickLetter(letter)}>
            <Letterbox letter={letter}
                       backgroundColor="{$letterStates[letter] === 'incorrect' ? '#5b5b5b' : '#a1a1a1'}"
                       width="{letter.length > 1 ? '2.3em' : '0.9em'}"
                       borderRadius="6px"
            />
        </li>
            {/each}
    </ul>
    {/each}
    <!--
    <p>{innerWidth}, {outerWidth}</p>
    <p>{innerHeight}, {outerHeight}</p>
    -->
</div>

<style>
    .keyboard {
        width: 100%;
    }
    .letter-list {
        list-style-type: none;
        padding: 0;
        margin: 0;
        margin-bottom: 4px;
        white-space: nowrap;
    }
    .word-letters {
        display: inline-block;
        margin: 0 2px;
    }
</style>