<script lang="ts">
    import Letterbox from "./Letterbox.svelte";
    export let guess;
    export let answer;
    export let reveal = false;
    const letters = "abcdefghijklmnopqrstuvwxyz".split("");
    let letterStates = [];
    $: if (!reveal && guess && answer) {
        //console.log(guess);
        let _letterStates = [];
        for (let i = 0; i < guess.length; i++) {
            //console.log(i, guess[i]);
            _letterStates.push({id: i, letter: guess[i], state: "empty"})
        }
        for (let i = 0; i < answer.length - guess.length; i++) {
            _letterStates.push({id: i+guess.length, letter: '\xa0', state: "empty"})
        }
        letterStates = _letterStates;
        //console.log("dont reveal...", letterStates)
    }
    $: if (reveal && guess && guess !== '' && answer) {
        console.log("reveal guess:", guess)
        guess = guess.toLowerCase();
        answer = answer.toLowerCase();
        let counts = {};
        letterStates = [];

        for (let letter of letters) {
            counts[letter] = 0;
        }
        for(let i = 0; i < guess.length; i++) {
            //let a = answer.at(i);
            let a = answer.substring(i, i+1);
            counts[a] += 1;
        }

        // two pass algorithm
        // pass 1: check if letter is in correct place
        for(let i = 0; i < guess.length; i++) {
            // should really make sure index not out of bounds...
            let g = guess.substring(i, i+1);
            let a = answer.substring(i, i+1);
            // remember default state is incorrect!
            let letterState = {id: i, letter: g, state: "incorrect"};
            if (g === a) {
                counts[a] -= 1;     // let pass two know that this letter is consumed
                letterState["state"] = "correct";
            }
            letterStates = [... letterStates, letterState];
        }

        // pass 2: check if letter can be placed elsewhere in incorrect places
        for(let i = 0; i < guess.length; i++) {
            // should really make sure index not out of bounds...
            let g = guess.substring(i, i+1);
            let a = answer.substring(i, i+1);

            if (g !== a) {
                // only check if letter is wrong
                if (counts[g] > 0) {
                    // letter can be placed elsewhere
                    letterStates[i]["state"] = "possible";
                    counts[g] -= 1;
                }
            }
        }
        letterStates = letterStates;
    }
    $: if (guess === '' && answer) {
        let _letterStates = [];
        for (let i = 0; i < answer.length; i++) {
            _letterStates.push({id: i, letter: '\xa0', state: "empty"})
        }
        letterStates = _letterStates;
    }
</script>

<div>
    <ul class="letter-list">
        {#each letterStates as l (l.id)}
            <li class="word-letters">
                <Letterbox letter={l.letter} state={l.state} color="{reveal ? '#000' : '#ffffff'}" />
            </li>
        {/each}
    </ul>
</div>

<style>
    .letter-list {
        list-style-type: none;
        padding: 0;
        margin: 0;
        white-space: nowrap;
    }
    .word-letters {
        display: inline-block;
        margin: 0 2px;
    }
</style>